import axios from "../plugins/axios";


const update = ({api,id=null,dataname=null,_page=1,_limit=10,type=null})=>(dispatch,getState)=>(
        axios({
            url:id?`/api/${api}/${id}`:`/api/${api}`,
            params:{_page,_limit}
        }).then(
            res=>{
                dispatch({type,payload: res.data.data})
            }
        )
)

const checkUser = ({method='get',username,password,api})=>(dispatch)=>(
    axios({
        url:`/api/${api}`,
        method,
        params: method === 'get' ? {username, password}: null,
        data: method === 'post' ? {username, password}: null,
    }).then(
        res=>{
            console.log(res.data)
            dispatch({type: "CHECK_USER",payload: res.data})
            return res.data;
        }
    )
)

const isCar = ({dataFrom})=>(dispatch)=>(
       dispatch({type: "IS_CAR",payload: dataFrom})
)
export {update,checkUser,isCar};













