import * as types from './types'

export default (state,{type,payload})=>{
    let isHave = true;
    let car = [...state.car];
    let allPriceS = state.allPriceS;
    switch (type) {
        case types.VIEW_DETAIL:
            return {...state,detail:payload};
        case types.VIEW_ADDCAE:
            //如果有重复的 不覆盖 个数++
            car.map((value,idx)=>{
                if(value['_id']==payload['_id']){
                    isHave=false;
                    state.car[idx].num++
                }
            })
            if(isHave){
                car.push(payload)
            }else {
                isHave=true;
            }
            allPriceS=allPriceS+(payload.num+1)*payload.newPrice;
            console.log(allPriceS)
            return {...state,car,allPriceS};
        case types.CHECK_USER:
            return {...state, user:payload};
        case types.IS_CAR:
            return {...state, isCar:payload};
        case types.CLEAR_USER:
            return {...state, user:{
                    err:1,
                    msg:"未登录",
                    data:{}
                }};
        default:
            return state;
    };
}
