import React from 'react';
import LoginReg from "../components/loginReg";
import Footer from "../components/Footer";

import store from "../plugins/redux";
import {checkUser} from "../store/actions";
import {connect} from 'react-redux';


class Login extends React.Component{
    state={
        username:"",
        password:"",
        msg:'',
        msg2:'账号或密码错误'
    }
    render(){
        let {isCar}=this.props;
        return(
            <div>
                <LoginReg dataname="登录" msg2={this.state.msg2} eventBind={()=>this.Login(isCar)} eventChange = {this.eventChange} msg={this.state.msg}/>
                <Footer/>
            </div>
        )
    }
    eventChange=(ev)=>{
        this.setState({
            [ev.target.name]:ev.target.value
        })
    };
    Login=(isCar)=>{
        store.dispatch(checkUser({
            api:'login',
            method:'post',
            username:this.state.username,
            password:this.state.password
        })).then(
            res=>{
                console.log(res)
                if(!res.err){
                    //登录成功
                    window.localStorage.setItem('react_login_app',JSON.stringify(res));
                    this.props.history.push('/'+isCar)
                }else {
                    this.setState({
                        msg:'登录失败'
                    })
                }
            }
        )
    }
}
const initMapStateToProps = state=>({
    isCar:state.isCar
});

export default connect(initMapStateToProps,null)(Login)