import React from 'react';
import { InputNumber,Button  } from 'antd';
import {Link} from "react-router-dom"
import {connect} from 'react-redux';
import {update} from "../store/actions";


class Detail extends React.Component{
  constructor(props){
    super(props);
    props.getDetail('list',this.props.match.params.id);
  }
  render(){
    let {detail,history,addCar} = this.props;
    return(
        <div >
          {
            detail && <div className="maincont">
                <header>
                  <a className="back-off fl" onClick={()=>{history.go(-1)}}>
                    <span className="glyphicon glyphicon-menu-left"></span>
                  </a>
                  <div className="head-mid">
                    <h1>产品详情</h1>
                  </div>
                </header>
                <div id="sliderA" className="slider">
                  <img src={React.baseUrl+detail.icon}/>
                </div>
                <table className="jia-len">
                  <tbody>
                  <tr>
                    <th><strong className="orange">￥{detail.newPrice}</strong></th>
                    <td></td>
                    <td>
                      <InputNumber min={1} max={10} defaultValue={1}/>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <strong>{detail.product}</strong>
                    </td>
                    <td align="right">
                      <Button type="danger" onClick={()=>{addCar('list',detail['_id'])}}>加购</Button>
                      {/*  点击时把当前商品加入购物车*/}
                    </td>
                    <td align="right">
                      <Link to={'/car'}>
                        <Button type="danger" onClick={()=>{addCar('list',detail['_id'])}}>购买</Button>
                      </Link>
                    </td>
                  </tr>
                  </tbody>
                </table>
                <div className="height2"></div>
                <div className="zhaieq">
                  <a className="zhaiCur">商品简介</a>
                  <a>商品参数</a>
                  <a >订购列表</a>
                  <div className="clearfix"></div>
                </div>
            </div>
          }
        </div>
    )
  }
}
const initMapStateToProps = state=>({
  detail: state.detail
});
const initMapDispatchToProps = dispatch=>({
  getDetail:(api,id)=>dispatch(update({
    api,
    id,
    type:'VIEW_DETAIL'
  })),
  addCar:(api,id)=>dispatch(update({
    api,
    id,
    type:'VIEW_ADDCAE'
  }))
});
export default connect(initMapStateToProps,initMapDispatchToProps)(Detail)

















