import React from 'react';
import Footer from "../components/Footer";
import ListSingle from "../components/listSingle"

export default class List extends React.Component {
    back=()=>{
        this.props.history.go(-1)
    }
    render() {
        return (
            <div className="maincont">
                <header>
                    <a className="back-off fl" onClick={this.back}>
                        <span className="glyphicon glyphicon-menu-left"></span>
                    </a>
                    <div className="head-mid">
                        <form action="#" method="get" className="prosearch"><input type="text"/></form>
                    </div>
                </header>
                <ul className="pro-select">
                    <li className="pro-selCur"><a href="#">新品</a></li>
                    <li><a href="#">销量</a></li>
                    <li><a href="#">价格</a></li>
                </ul>
                <ListSingle/>
                <div className="height1"></div>
                <Footer/>
            </div>
        )
    }
}