import React from 'react';
import "../assets/css/listSingle.css"
import {Link} from "react-router-dom"


export default class ListSingle extends React.Component{
    state={
        list:[]
    }
    async componentDidMount() {
        let res = await React.axios({
            url:'/api/list',
            params:{
                _page:1,
                _limit:6
            }
        })
        this.setState({list:res.data.data});
    }
    render(){
        return(
            <div className="prolist">
                {
                    this.state.list && this.state.list.map(item=>(
                            <dl key={item['_id']} className={"listSingleImg"}>
                                <Link to={`/detail/${item['_id']}`}>
                                    <dt><img src={React.baseUrl+item.icon} /></dt>
                                    <dd>
                                        <h3>{item.product}</h3>
                                        <div className="prolist-price"><strong>￥{item.newPrice}</strong> <span>￥{item.oldPrice}</span></div>
                                        <div className="prolist-yishou"><span>{(item.newPrice/item.oldPrice).toFixed(2)}折</span> <em>已售：{item.sale}</em></div>
                                    </dd>
                                    <div className="clearfix"></div>
                                </Link>
                            </dl>
                    ))
                }
            </div>
        )
    }
}