import React from 'react';
import LoginReg from "../components/loginReg";
import Footer from "../components/Footer";
import Button from "../components/button"


export default class Login extends React.Component{
    state={
        username:"",
        password:"",
        msg:'',
        msg2:'账号已存在'
    }
    render(){
        return(
            <div>
                <LoginReg dataname="注册" msg2={this.state.msg2} eventBind={this.Reg} eventChange = {this.eventChange} msg={this.state.msg}/>
                <Footer/>
            </div>
        )
    }
    eventChange=(ev)=>{
        this.setState({
            [ev.target.name]:ev.target.value
        })
    };
    Reg=()=>{
        React.axios({
            url:'/api/reg',
            method:'post',
            data: {
                username:this.state.username,
                password:this.state.password
            }
        }).then(
            res=>{
                res.data.err?this.setState({msg:'注册失败'}):console.log('注册成功')
            }
        )
    }
}