import React from 'react';
import {NavLink} from "react-router-dom"

import {connect} from 'react-redux';
import {isCar} from "../store/actions";

class LoginReg extends React.Component{
    render(){
        let {isCart} = this.props;
        return(
            <div className="footNav">
                <dl>
                    <NavLink to="/home" activeClassName="activefooter">
                        <dt><span className="glyphicon glyphicon-home"></span></dt>
                        <dd>主页</dd>
                    </NavLink>
                </dl>
                <dl>
                    <NavLink to="/list"  activeClassName="activefooter">
                        <dt><span className="glyphicon glyphicon-th"></span></dt>
                        <dd>所有商品</dd>
                    </NavLink>
                </dl>
                <dl onClick={()=>isCart('car')}>
                    <NavLink to="/car"  activeClassName="activefooter">
                        <dt><span className="glyphicon glyphicon-shopping-cart"></span></dt>
                        <dd>购物车</dd>
                    </NavLink>
                </dl>
                <dl onClick={()=>isCart('user')}>
                    <NavLink to="/user"  activeClassName="activefooter">
                        <dt><span className="glyphicon glyphicon-user"></span></dt>
                        <dd>我的</dd>
                    </NavLink>
                </dl>
                <div className="clearfix"></div>
            </div>
        )
    }
}

const initMapStateToProps = state=>({
    isCar:state.isCar
});
const initMapDispatchToProps = dispatch=>({
    isCart:(data)=>dispatch(isCar({
        dataFrom:data,
        type:'IS_CAR'
    }))
});
export default connect(initMapStateToProps,initMapDispatchToProps)(LoginReg)