import React from 'react';
import { Carousel } from 'antd';
import {Link} from "react-router-dom"
import Footer from "../components/Footer";
import ListSingle from "../components/listSingle"

import {isCar} from "../store/actions";

import Button from "../components/button"

import {connect} from 'react-redux'

class Home extends React.Component{
    render(){
        let {data,err,isCart} = this.props;
        return(
            <div className="maincont">
                <div className="head-top">
                    <img src={require("../assets/images/rb.jpg")} />
                    <dl>
                        <dt><Link to="/user">
                            {
                                err ? null:<img src={React.baseUrl+data.icon} />
                            }
                        </Link></dt>
                        <dd>
                            {
                                err ? <Link className="username" to={'/login'}>请先登录</Link>:<h1 className="username">{data.nikename}</h1>
                            }
                            <ul>
                                <li><Link to="/list"><strong>34</strong><p>全部商品</p></Link></li>
                                <li><a href="#"><span className="glyphicon glyphicon-star-empty"></span>
                                    <p>收藏本店</p></a></li>
                                <li style={{background:'none'}}>
                                    <a href="#">
                                        <span className="glyphicon glyphicon-picture"></span>
                                        <p>二维码</p>
                                    </a>
                                </li>
                                <div className="clearfix"></div>
                            </ul>
                        </dd>
                        <div className="clearfix"></div>
                    </dl>
                </div>
                <form action="#" method="get" className="search">
                    <input type="text" className="seaText fl"/>
                    <input type="submit" value="搜索" className="seaSub fr"/>
                </form>
                <ul className="reg-login-click" onClick={()=>isCart('home')}>
                    <Button proName="登录" toName="/login"/>
                    <Button proName="注册" classN="rlbg" toName="/reg"/>
                    <div className="clearfix"></div>
                </ul>
                <Carousel autoplay id="sliderA" className="slider">
                    <div>
                        <div style={{height:'25rem',overflow:'hidden'}}>
                            <img src={require("../assets/images/img1.jpg")} style={{marginTop:'-10rem'}}/>
                        </div>
                    </div>
                    <div>
                        <div style={{height:'25rem',overflow:'hidden'}}>
                            <img src={require("../assets/images/img2.jpg")} style={{marginTop:'-10rem'}}/>
                        </div>
                    </div>
                    <div>
                        <div style={{height:'25rem',overflow:'hidden'}}>
                            <img src={require("../assets/images/img3.jpg")} style={{marginTop:'-10rem'}}/>
                        </div>
                    </div>
                </Carousel>
                <ul className="pronav">
                    <Button proName="晋恩干红" toName="/list"/>
                    <Button proName="万能手链" toName="/list"/>
                    <Button proName="高级手镯" toName="/list"/>
                    <Button proName="特异戒指" toName="/list"/>
                    <div className="clearfix"></div>
                </ul>
                <div className="index-pro1">
                    <div className="index-pro1-list">
                        <dl>
                            <dt><a><img src={require("../assets/images/pro1.jpg")}/></a></dt>
                            <dd className="ip-text"><a>这是产品名称</a></dd>
                            <dd className="ip-price"><strong>¥299</strong> <span>¥599</span></dd>
                        </dl>
                    </div>
                    <div className="index-pro1-list">
                        <dl>
                            <dt><a><img src={require("../assets/images/pro1.jpg")}/></a></dt>
                            <dd className="ip-text"><a>这是产品名称</a></dd>
                            <dd className="ip-price"><strong>¥299</strong> <span>¥599</span></dd>
                        </dl>
                    </div>
                    <div className="clearfix"></div>
                </div>
                <div className="copyright">Copyright &copy; <span className="blue">这是就是三级分销底部信息</span></div>
                <div className="height1"></div>
                <Footer/>
            </div>
        )
    }
}
const initMapStateToProps = state=>({
    err:state.user.err,
    data: state.user.data
});
const initMapDispatchToProps = dispatch=>({
    isCart:(data)=>dispatch(isCar({
        dataFrom:data,
        type:'IS_CAR'
    }))
});
export default connect(initMapStateToProps,initMapDispatchToProps)(Home)