import React from 'react';
import { InputNumber  } from 'antd';
import {connect} from 'react-redux';


class Car extends React.Component{
    //如果用户登录过 用状态管理中的数据进行渲染
    //如果没登录 让用户去登录页面
    render(){
        let {car,allPriceS,isCar} = this.props;
        let allPrice = ()=>{
            let price=0;
            for(let i=0;i++;i<car.length){
                price = price+car[i].newPrice*(car[i].num+1);
                console.log(price)
            }
        };
        allPrice();
        return(
            <div className="maincont">
                <header>
                    <a className="back-off fl" onClick={()=>{this.props.history.go(-1)}}>
                        <span className="glyphicon glyphicon-menu-left"></span>
                    </a>
                    <div className="head-mid">
                        <h1>购物车</h1>
                    </div>
                </header>
                <div className="head-top">
                    <img src={require("../assets/images/rb.jpg")}/>
                </div>
                <table className="shoucangtab">
                    <tbody>
                    <tr>
                        <td width="75%"><span className="hui">购物车共有：<strong className="orange">{car.length}</strong>件商品</span></td>
                        <td width="25%" align="center" >
                            <span className="glyphicon glyphicon-shopping-cart"></span>
                        </td>
                    </tr>
                    </tbody>
                </table>

                <div className="dingdanlist">
                    <table>
                        <thead>
                        <tr>
                            <td width="100%" colSpan="4"><a ><input type="checkbox" name="1"/> 全选</a>
                            </td>
                        </tr>
                        </thead>

                        {
                            car.map((item,idx)=>(
                                <tbody key={idx}>
                                    <tr >
                                        <td width="4%"><input type="checkbox" name="1"/></td>
                                        <td className="dingimg" width="15%"><img src={React.baseUrl+item.icon}/></td>
                                        <td width="50%">
                                            <h3>{item.product}</h3>
                                        </td>
                                        <td align="right">
                                            <p>￥{item.newPrice}</p>
                                            <InputNumber min={1} max={30} value={item.num+1}/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th colSpan="4"><strong className="orange">¥{item.newPrice*(item.num+1)}</strong></th>
                                    </tr>
                                </tbody>
                            ))
                        }
                    </table>
                </div>
                <div className="height1"></div>
                <div className="gwcpiao">
                    <table>
                        <tbody>
                        <tr>
                            <th width="10%">
                                <a>
                                    <span className="glyphicon glyphicon-menu-left"></span>
                                </a>
                            </th>
                            <td width="50%">总计：
                                  {
                                      allPriceS && <strong className="orange">¥{allPriceS}</strong>
                                  }
                            </td>
                            <td width="40%"><a className="jiesuan">去结算</a></td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
}


const initMapStateToProps = state=>({
    car: state.car,
    allPriceS:state.allPriceS,
});

export default connect(initMapStateToProps,null)(Car)