import React from 'react';
import {Link} from "react-router-dom"
import { Alert } from 'antd';

export default class LoginReg extends React.Component{
    render(){
        return(
            <div className="maincont">
                <header>
                    <a onClick={()=>{window.history.go(-1)}} className="back-off fl">
                        <span className="glyphicon glyphicon-menu-left"></span>
                    </a>
                    <div className="head-mid">
                        <h1>会员{this.props.dataname}</h1>
                    </div>
                </header>
                <div className="head-top">
                    <img src={require("../assets/images/rb.jpg")}/>
                </div>
                <div  className="reg-login">
                    <h3><span>{this.props.dataname=='注册'?'已有账号':'还没有三级分销账号'}</span>？点此<Link to={this.props.dataname=='注册'?'/login':'/reg'} className="orange">{this.props.dataname=='注册'?'登录':'注册'}</Link></h3>
                    <div className="lrBox">
                        <div className="lrList"><input type="text" placeholder="输入用户名" name='username' onChange={this.props.eventChange}/></div>
                        <div className="lrList"><input type="text" placeholder="输入密码" name='password' onChange={this.props.eventChange}/></div>
                    </div>
                    <div className="lrSub">
                        <input type="submit" value={`立即${this.props.dataname}`} onClick={this.props.eventBind}/>
                    </div>
                </div>
                {this.props.msg && <div style={{width:"26rem",height:"15",margin:'0 auto'}} className={'box111'} >
                    <Alert
                        message={this.props.msg}
                        description={this.props.msg2}
                        type="error"
                        showIcon
                    />
                </div>}
                <div className="height1"></div>
            </div>
        )
    }
}
