import React from 'react';
import Footer from "../components/Footer";
import {isCar} from "../store/actions";
import {connect} from 'react-redux';

import store from "../plugins/redux";


class User extends React.Component{
  render(){
    let {data} = this.props;
    return(
        <div className="maincont">
          <div className="userName">
            <dl className="names">
              <dt><img src={React.baseUrl+data.icon}/></dt>
              <dd>
                <h3>{data.nikename}</h3>
              </dd>
              <div className="clearfix"></div>
            </dl>
            <div className="shouyi">
              <dl>
                <dt>我的余额</dt>
                <dd>0.00元</dd>
              </dl>
              <dl>
                <dt>我的积分</dt>
                <dd>0</dd>
              </dl>
              <div className="clearfix"></div>
            </div>
          </div>

          <ul className="userNav">
            <li><span className="glyphicon glyphicon-list-alt"></span><a >我的订单</a></li>
            <div className="height2"></div>
            <div className="state">
              <dl>
                <dt><a><img src={require("../assets/images/user1.png")}/></a></dt>
                <dd><a>待支付</a></dd>
              </dl>
              <dl>
                <dt><a><img src={require("../assets/images/user2.png")}/></a></dt>
                <dd><a>代发货</a></dd>
              </dl>
              <dl>
                <dt><a><img src={require("../assets/images/user3.png")}/></a></dt>
                <dd><a>待收货</a></dd>
              </dl>
              <dl>
                <dt><a><img src={require("../assets/images/user4.png")}/></a></dt>
                <dd><a>全部订单</a></dd>
              </dl>
              <div className="clearfix"></div>
            </div>
            <li><span className="glyphicon glyphicon-usd"></span><a>我的优惠券</a></li>
            <li><span className="glyphicon glyphicon-map-marker"></span><a>收货地址管理</a></li>
            <li><span className="glyphicon glyphicon-star-empty"></span><a>我的收藏</a></li>
            <li><span className="glyphicon glyphicon-heart"></span><a>我的浏览记录</a></li>
            <li><span className="glyphicon glyphicon-usd"></span><a>余额提现</a></li>
          </ul>
          <div className="lrSub">
            <a onClick={this.logout}>退出登录</a>
          </div>
          <div className="height1"></div>
          <Footer/>
        </div>
    )
  }
  logout=()=>{
    React.axios({
      url:'/api/logout',
      method:'delete',
    }).then(
        res=>{
          console.log(res.data);
          if(!res.data.err){
            //注销成功 清除cookie 清除localStorage
            console.log(res.data);
            window.localStorage.removeItem('react_login_app')
            store.dispatch({type:'CLEAR_USER',})
          }else{

          }
        }
    )
  }
}
const initMapStateToProps = state=>({
  isCar:state.isCar
});
const initMapDispatchToProps = dispatch=>({
  isCart:()=>dispatch(isCar({
    dataFrom:"user",
    type:'IS_CAR'
  }))
});
export default connect(initMapStateToProps,initMapDispatchToProps)(User)